import { isPoneAvailable } from "@/utils";
import { login, register } from "@/api";
export default {
  data() {
    return {
      registerPopUpShow: false, // 注册弹框是否显示
      formData: {
        logoPhone: "", // 登录手机号
        logoPassword: "", // 登录密码
        registerPhone: "", // 注册手机号
        registerPassword: "", // 注册密码
        PasswordAffirm: "" // 确认密码
      },
      hint: false, // 提醒是否显示
      hintText: ""
    };
  },
  methods: {
    login() {
      // 登录
      if (!isPoneAvailable(this.formData.logoPhone)) {
        uni.showToast({
          title: "请输入正确的手机号", //提示的内容,
          icon: "none", //图标,
          duration: 1500, //延迟时间,
          mask: false //显示透明蒙层，防止触摸穿透,
        });
        return;
      }
      if (this.formData.logoPassword == "") {
        uni.showToast({
          title: "请将信息填写完整", //提示的内容,
          icon: "none", //图标,
          duration: 1500, //延迟时间,
          mask: false //显示透明蒙层，防止触摸穿透,
        });
        return;
      }
      let data = {
        phone: this.formData.logoPhone,
        upwd: this.formData.logoPassword
      };
      login(data).then(res => {
        if (res.ok) {
          uni.showToast({
            title: "登录成功", //提示的内容,
            icon: "none", //图标,
            duration: 1500, //延迟时间,
            mask: true //显示透明蒙层，防止触摸穿透,
          });
          setTimeout(() => {
            wx.redirectTo({ url: "/pages/basicSetup?uid=" + res.data.uid });
          }, 1200);
        } else {
          uni.showToast({
            title: res.msg, //提示的内容,
            icon: "none", //图标,
            duration: 1500, //延迟时间,
            mask: true //显示透明蒙层，防止触摸穿透,
          });
        }
        console.log(res, "登录情况");
      });
    },
    register() {
      // 注册
      if (!isPoneAvailable(this.formData.registerPhone)) {
        this.hint = true;
        this.hintText = "请输入正确的手机号";
        return;
      }
      if (this.formData.registerPassword != this.formData.PasswordAffirm) {
        this.hint = true;
        this.hintText = "两次密码输入不一致";
        return;
      }
      if (this.formData.registerPassword == "") {
        this.hint = true;
        this.hintText = "请将信息填写完整";
        return;
      }
      this.hint = false;
      let data = {
        phone: this.formData.registerPhone,
        upwd: this.formData.registerPassword
      };
      register(data).then(res => {
        if (res.ok) {
          uni.showToast({
            title: "注册成功", //提示的内容,
            icon: "none", //图标,
            duration: 1500, //延迟时间,
            mask: true //显示透明蒙层，防止触摸穿透,
          });
          setTimeout(() => {
            this.registerPopUp();
          }, 1000);
        } else {
          uni.showToast({
            title: res.msg, //提示的内容,
            icon: "none", //图标,
            duration: 1500, //延迟时间,
            mask: true //显示透明蒙层，防止触摸穿透,
          });
        }
        console.log(res, "登录情况");
      });
      console.log(this.formData, isPoneAvailable(this.formData.registerPhone));
    },
    registerPopUp() {
      // 去注册弹框控制
      this.registerPopUpShow = !this.registerPopUpShow;
      this.formData.registerPhone = "";
      this.formData.registerPassword = "";
      this.formData.PasswordAffirm = "";
      return;
    },
    stop() {
      // 停止冒泡
      return;
    }
  }
};
